html {
  scroll-behavior: smooth;
}

body {
  font-family: "TTNormsPro", 'sans-serif';
  font-weight: 400;
  background: #1c242c;
}

.container {
  max-width: 1330px;
  margin: 0 auto;
  padding: 0 35px;
  box-sizing: border-box;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 68px;
  }
}

@media screen and (min-width: 1101px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  padding: 29px 40px;
  width: 100%;
  right: 0;
  box-sizing: border-box;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 46px 60px 46px 79px;
  }
}

@media screen and (min-width: 1101px) {
  .header {
    padding: 46px 20px 46px 20px;
    max-width: 1330px;
    margin: 0 auto;
    box-sizing: border-box;
    left: 0;
  }
}

.header__logo {
  z-index: 2;
}

.header__logo img {
  transform: translateY(5px);
  max-width: 78px;
}

@media screen and (min-width: 768px) {
  .header__logo img {
    max-width: 128px;
  }
}

.header__menu {
  padding: 35px;
  margin: 0;
  list-style-type: none;
  position: absolute;
  text-align: left;
  visibility: hidden;
  z-index: -1;
  opacity: 0;
  width: 100%;
  height: 100vh;
  height: var(--app-height);
  top: 0;
  right: 0;
  background-color: #1f242d;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: all 0.5s ease;
}

@media screen and (min-width: 768px) {
  .header__menu {
    padding: 35px 80px;
  }
}

@media screen and (min-width: 1101px) {
  .header__menu {
    visibility: visible;
    z-index: 1;
    opacity: 1;
    position: unset;
    flex-direction: row;
    height: auto;
    width: auto;
    background: transparent;
    transition: unset;
    padding: 0;
    margin-left: 310px;
  }
}

.header__menu li a {
  color: #f0f0f1;
  text-decoration: none;
  font-weight: 700;
  font-size: 34px;
  line-height: 48px;
  display: block;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__menu li a {
    font-size: 62px;
    line-height: 80px;
  }
}

@media screen and (min-width: 1101px) {
  .header__menu li a {
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    color: #f2f1ef;
    margin-right: 45px;
  }
}

.header__menu li a:hover:before {
  content: "";
  height: 1px;
  width: 100%;
  background: #f0f0f1;
  position: absolute;
  bottom: -5px;
  animation: slide 0.5s ease-in-out;
}

@keyframes slide {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.fade-in {
  position: fixed;
  animation: sticky 0.5s ease-in-out;
  background: #1f242d;
  z-index: 9999;
  height: 99px;
}

@media screen and (min-width: 768px) {
  .fade-in {
    padding: 25px 80px 25px 80px;
  }
}

@media screen and (min-width: 1101px) {
  .fade-in {
    padding: 25px 80px 25px 80px;
    max-width: 100%;
  }
}

@keyframes sticky {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.header-menu__hamburger {
  z-index: 2;
}

@media screen and (min-width: 1101px) {
  .header-menu__hamburger {
    display: none;
  }
}

.active-state {
  overflow: hidden;
}

.active {
  visibility: visible;
  z-index: 1;
  opacity: 1;
}

.header__lang-switch {
  display: none;
  color: #f2f1ef;
  font-size: 18px;
  z-index: 1;
}

.header__lang-switch a {
  font-size: 18px;
  font-weight: 400;
  color: #f2f1ef;
  text-decoration: none;
}

@media screen and (min-width: 1101px) {
  .header__lang-switch {
    display: block;
  }
}

.active-lang {
  opacity: .5;
}

.header-menu__hamburger span {
  position: relative;
  display: block;
  width: 25px;
  height: 2px;
  background-color: #fff;
  z-index: 1;
  -ms-transform-origin: 4px 0;
  transform-origin: 4px 0;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}

@media screen and (min-width: 768px) {
  .header-menu__hamburger span {
    width: 40px;
  }
}

.header-menu__hamburger span:not(:last-child) {
  margin-bottom: 4px;
}

@media screen and (min-width: 768px) {
  .header-menu__hamburger span:not(:last-child) {
    margin-bottom: 7px;
  }
}

.header-menu__hamburger span:first-child {
  -ms-transform-origin: 0% 0%;
  transform-origin: 0% 0%;
}

.animate span:nth-child(2) {
  -ms-transform-origin: 0% 100%;
  transform-origin: 0% 100%;
}

.animate span:first-child {
  opacity: 1;
  transform: rotate(45deg) translate(0, -4px);
}

@media screen and (min-width: 768px) {
  .animate span:first-child {
    transform: rotate(45deg) translate(-3px, -7px);
  }
}

.animate span:nth-child(2) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

.animate span:nth-child(3) {
  transform: rotate(-45deg) translate(0, 0px);
}

@media screen and (min-width: 768px) {
  .animate span:nth-child(3) {
    transform: rotate(-45deg) translate(-1px, 0);
  }
}

